
import EncounterMixinVue from "@/views/EncounterMixin.vue";
import { FieldType } from "@/components/Forms/BaseFormElements";
import { defineComponent } from "vue";
import { IonPage } from "@ionic/vue";
import { Field, Option } from "@/components/Forms/FieldInterface";
import { HtsRecencyService } from "../../services/hts_recency_service";

export default defineComponent({
  components: { IonPage },
  mixins: [EncounterMixinVue],
  data: () => ({
    service: {} as HtsRecencyService,
  }),
  watch: {
    ready: {
      handler(ready: boolean) {
        if (ready) {
          this.service = new HtsRecencyService(
            this.patientID,
            this.providerID
          );
          this.fields = [
            this.recencyTestField()
          ];
        }
      },
      immediate: true,
    },
  },
  methods: {
    onFinish(formData: any, computedData: any) {
      this.saveFormEncounterAndProceed({
        encounterTypeID: this.service.encounterTypeID,
        formData,
        computedData,
      })
    },
    recencyTestField(): Field {
      return {
        id: "recency_test",
        helpText: "Recency Test",
        type: FieldType.TT_SELECT,
        isRequired: () => true,
        finalComputedValue: (v: Option) => {
          return {
            obs: this.service.buildValueCoded("Recency Test", v.value),
          };
        },
        options: () => {
          return this.mapStrToOptions([
            "Long-Term",
            "Recent",
            "Negative",
            "Invalid",
            "NOT done"
          ]);
        },
      };
    },
  },
});
